.App {
  background-color: #fff;
  animation:appear 3s;
}

@keyframes appear {
  from {opacity: 0;}
  to {opacity:100;}
} 

.footer {
  min-height:200px;
}

.grid {
  display:grid;
  grid-template-columns: auto auto auto;
}

.grid-image {
  min-width:300px;
  max-width:300px;
  display:block;
  margin-top:20%;
  margin-left:auto;
  margin-right:auto;
}


.grid-item {
  display:block;
  margin:auto;
  transition:1s;
}

.grid-item:hover {
  transform:translateY(-20px);
}


